@media screen and (min-width: $fhd) {
	.content__slide-3 .slide-3__nav .slide-3__nav-item .s3__nav-item__btn {
		font-size: 20px;
		padding: 2.3vh;
	}
	
	.content__slide-3 .slide-3__chapter .chapter__hero {
		padding-left: 400px;
	}
	
	.content__slide-3 .slide-3__nav {
		width: 320px;
	}
	
	
	.content__slide-3 .s3-ch3__list .item--custom .item__content p {
		font-size: 24px;
	}
	
	.content__slide-3 .s3-ch3__list .item .item__text p {
		font-size: 24px;
	}
}



@media screen and (max-width: $mobile) {
	.content__slide-3 .slide-3__nav {
		display: none;
	}
	
	.slide-3__chapter-4 .custom-ul li {
		padding-left: 28px;
	}
	
	.content__slide-3 .slide-3__chapter {
		flex-direction: column;
		width: 100vw;
		overflow: hidden;
	}
	
	.content__slide-3 .slide-3__chapter .chapter__hero {
		padding: 80px 16px 8px;
		width: 100%;
		max-width: 100%;
		min-width: 100%;
		height: auto;
	}
	
	.s3-ch4__deco {
		display: none;
	}
	
	.content__slide-3 .slide-3__chapter h2 {
		font-size: 24px;
		margin-bottom: 16px;
		
		br {
			display: none;
		}
	}
	
	.content__slide-3 .slide-3__chapter .chapter-hero__info {
		width: auto;
		margin-left: 48px;
	}
	
	.content__slide-3 .slide-3__chapter .chapter-hero__info::before {
		width: 32px;
		left: -48px;
	}
	
	.content__slide-3 .slide-3__chapter .chapter-hero__image {
		position: relative;
		display: none;
	}
	
	.content__slide-3 .slide-3__chapter-1 .s3-ch1__directions {
		padding: 16px 0;
	}
	
	.content__slide-3 .slide-3__chapter h3 {
		margin: 16px 0;
		font-size: 24px;
	}
	
	.content__slide-3 .slide-3__chapter-1 .s3-ch1__directions h3 {
		margin-bottom: 16px;
		padding: 0 16px;
	}
	
	.content__slide-3 .slide-3__chapter-1 .s3-ch1__directions .s3-ch1__directions-list {
		flex-direction: column;
		
		width: 100%;
	}
	
	.content__slide-3 .slide-3__chapter-1 .s3-ch1__directions .dir-list__item {
		margin: 0 0 80px;
		width: auto;
	}
	
	.content__slide-3 .slide-3__chapter-1 .s3-ch1__directions .dir-list__item h4 {
		padding: 0 16px;
	}
	
	.content__slide-3 .slide-3__chapter-1 .s3-ch1__article {
		width: 100%;
		max-width: 100%;
		min-width: 100%;
		
		display: flex;
		flex-direction: column;
	}
	
	.content__slide-3 .slide-3__chapter-1 .s3-ch1__article-text {
		padding: 16px;
	}
	
	.content__slide-3 .slide-3__chapter-1 .s3-ch1__experience .article-blue {
		margin: 0 0 48px;
	}
	
	.content__slide-3 .slide-3__chapter-1 .s3-ch1__experience {
		flex-direction: column;
	}
	
	.content__slide-3 .slide-3__chapter-1 .s3-ch1__experience .article-blue__wrapper {
		padding: 0;
	}
	
	.article-blue {
		width: 100%;
		max-width: 100%;
		min-width: 100%;
		padding: 32px 16px;
	}
	
	.content__slide-3 .slide-3__chapter .article-map {
		width: 100%;
		max-width: 100%;
		min-width: 100%;
		
		height: auto;
		
		padding: 0;
	}
	
	.content__slide-3 .slide-3__chapter .article-map .map-img {
		height: auto;
		margin-bottom: 32px;
		
		padding: 16px 8px;
	}
	
	.content__slide-3 .slide-3__chapter .article-map p {
		height: auto;
		
		padding: 16px;
	}
	
	
	.content__slide-3 .slide-3__chapter-2 .s3-ch2__range {
		flex-direction: column;
		padding: 16px 0;
		
		h2 {
			padding: 16px;
			margin: 0 0 16px;
		}
	}
	
	.content__slide-3 .slide-3__chapter-2 .range__grid-col .range__grid-item {
		margin: 0;
		
		width: 100%;
		max-width: 100%;
		min-width: 100%;
	}
	
	.content__slide-3 .slide-3__chapter-2 .range__grid-col {
		width: 100%;
		max-width: 100%;
		min-width: 100%;
		margin: 0;
		padding: 0;
	}
	
	.content__slide-3 .slide-3__chapter-2 .range__grid-col--2 .range__grid-item:nth-of-type(2),
	.content__slide-3 .slide-3__chapter-2 .range__grid-col--4 .range__grid-item:nth-of-type(1){
		margin: 0;
	}
	
	.content__slide-3 .slide-3__chapter-2 .range__grid-col .range__grid-item {
		margin-bottom: 48px !important;
	}
	
	//.content__slide-3 .slide-3__chapter-2 .range__grid-col--5 .range__grid-item {
	//	margin-bottom: 0 !important;
	//}
	
	.content__slide-3 .slide-3__chapter-2 .range__grid-col .range__grid-item .range__img-wrapper {
		width: 100%;
		max-width: 100%;
		min-width: 100%;
		
		margin-bottom: 8px;
		
		height: auto;
	}
	
	.content__slide-3 .slide-3__chapter-2 .range__grid-col .range__grid-item h4 {
		font-size: 16px;
		padding: 0 16px;
	}
	
	.content__slide-3 .s3-ch3__list {
		flex-direction: column;
	}
	
	.content__slide-3 .s3-ch3__list .item--custom {
		padding: 16px 8px;
		width: 100%;
		max-width: 100%;
		min-width: 100%;
		
		margin-top: 120px;
	}
	
	.content__slide-3 .s3-ch3__list .item--custom .item__content {
		//display: flex;
		display: block;
		grid-gap: 0;
	}
	
	.content__slide-3 .s3-ch3__list .item--custom .item__content h3 {
		transform: translateY(-120%);
		font-size: 28px;
	}
	
	.content__slide-3 .s3-ch3__list .item--custom .item__content svg {
		margin-right: 16px;
		margin-top: 0;
		margin-bottom: 16px;
		float: left;
		
		transform: translateY(6px);
	}
	
	.content__slide-3 .s3-ch3__list .item--custom .item__content p {
		font-size: 20px;
	}
	
	.content__slide-3 .s3-ch3__list .item {
		width: 100%;
		max-width: 100%;
		min-width: 100%;
		
		height: auto;
		
		margin-bottom: 0;
	}
	
	.content__slide-3 .s3-ch3__list .item--alt {
		flex-direction: column;
	}
	
	.content__slide-3 .s3-ch3__list .item .item__img {
		height: 50vh;
	}
	
	.content__slide-3 .s3-ch3__list .item .item__text {
		height: auto;
		padding: 80px 8px 32px 8px;
	}
	
	.content__slide-3 .s3-ch3__list .item svg {
		transform: none;
		top: 25vh;
		
		left: 8px;
	}
	
	.content__slide-3 .slide-3__chapter-4 .s3-ch4__list {
		//display: flex;
		//flex-direction: column;
		display: block;
	}
	
	.content__slide-3 .slide-3__chapter-4 .s3-ch4__list .list-title h2 {
		padding: 16px 8px;
		margin-bottom: 24px;
		margin-top: 32px;
	}
	
	.content__slide-3 .slide-3__chapter-4 .s3-ch4__list .item {
		flex-direction: column;
	}
	
	.content__slide-3 .slide-3__chapter-4 .s3-ch4__list .item__img {
		height: 50vh;
		width: 100%;
	}
	
	.content__slide-3 .slide-3__chapter-4 .s3-ch4__list .item__content {
		height: auto;
		padding: 16px 16px 80px 16px;
	}
	
	.content__slide-3 .slide-3__chapter-4 .s3-ch4__list .item__content p {
		font-size: 18px;
	}
}