@media screen and (max-width: $mobile) {
	.content__slide-1 .slide-1--s1 {
		padding: 80px 16px 16px;
		
		width: 100%;
		min-width: 100%;
		max-width: 100%;
	}
	
	.content__slide-1 .slide-1--s2 {
		width: 100%;
		min-width: 100%;
		max-width: 100%;
	}
	
	.content__slide-1 .slide-1--s2 .content__image-wrapper {
		padding: 16px;
		height: auto;
	}
	
	.content__slide-1 .slide-1--s2 .content__text {
		padding: 16px;
		height: auto;
	}
	
	.content__slide-1 .slide-1--s3 {
		width: 100%;
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
		
		.grid-child {
			height: 50vw;
			width: 50%;
			
			display: flex;
			flex-direction: row;
			align-items: center;
			justify-content: center;
			
			img {
				object-fit: contain;
				max-width: 80%;
			}
			
			&:nth-of-type(3) {
				background: transparent;
			}
			&:nth-of-type(6) {
				background: $sgs-blue;
			}
			&:nth-of-type(8) {
				background: transparent;
			}
			&:nth-of-type(9) {
				background: $sgs-blue;
			}
		}
		
		.grid-child--1 {
			width: 100vw;
			height: auto;
			padding: 16px;
			
			h2 {
				font-size: 24px;
			}
		}
	}
}