@media screen and (min-width: 1600px) {
	.slider__slide-description p {
		font-size: 26px;
	}
	
	.slider__slide-description .slider__more-details {
		font-size: 20px;
	}
	
	.main-screen__contacts .contacts__phone-number .number__city {
		font-size: 18px;
	}
	.main-screen__contacts .contacts__phone-number .number__num {
		font-size: 24px;
	}
}

@media screen and (max-width: 1024px) {
	.main-screen {
		display: flex;
		flex-direction: column;
		height: auto;
		overflow-y: auto;
	}
	
	.main-screen__side {
		width: 100%;
	}
	
	.main-screen__left-side {
		padding: 96px 12px 16px 12px;
	}
	
	.main-screen__contacts {
		display: none;
	}
	
	.slider__slide-description {
		padding: 16px 16px 88px;
		font-size: 16px;
	}
	
	.slider__slide-description h1 {
		font-size: 7.666vmin;
		margin-bottom: 24px;
	}
	.slider__slide-description h1.fz-31 {
		font-size: 6.5vmin;
	}
	
	.slider__more-details {
		width: 100%;
	}
	
	.slider__indicator {
		margin-left: 16px;
	}
}