@media screen and (min-width: $fhd) {
	.main-menu__nav-list .main-menu__nav-item {
		font-size: 32px;
	}
}

@media screen and (max-width: $mobile) {
	.main-menu {
		overflow-y: auto;
		flex-direction: column;
	}
	
	//.page-logo--fixed {
	//	position: relative;
	//}
	
	.main-menu__nav, .main-menu__contacts {
		width: 100%;
	}
	
	.contacts__header .contacts__data .contacts__phone, .contacts__header .contacts__data .contacts__email {
		height: 56px;
	}
	
	.main-menu__nav {
		padding-left: 24px;
		//margin-bottom: 24px;
	}
	
	.main-menu__nav-list {
		padding-top: 16px;
		padding-right: 24px;
		
		margin-bottom: 24px;
	}
	
	.custom-ul li {
		padding-left: 48px;
	}
	
	.main-menu__contacts {
		min-height: calc(100vh - 80px);
	}
	
	.contacts__header .contacts__title {
		height: auto;
		padding: 16px;
		display: flex;
		flex-direction: column;
		
		span {
			margin-bottom: 16px;
		}
	}
	
	.main-menu__address-buttons .address-button {
		padding: 8px;
	}
	
	.mapboxgl-canvas {
		width: 100% !important;
		height: 100% !important;
	}
	
	.contacts__geo {
		overflow: hidden;
		height: 20vh;
	}
	
	.contacts__geo .contacts__address {
		width: 100%;
		padding: 16px;
	}
}