html {
  height: 100%;
  overflow: hidden;
  box-sizing: border-box;
}

* {
  box-sizing: border-box;
  outline: none;
}

body {
  letter-spacing: -.02rem;
  font-family: $body-ff;
  font-size: 18px;
  line-height: 1.5;
  box-sizing: border-box;
  background: $dark-ocean;

  // background: rgba($color: #000000, $alpha: 0.5);
  display: flex;
  // flex-direction: row;
  height: 100%;
  // align-items: stretch;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.hero {
  width: 100%;
  height: 100%;
  
  overflow-x: visible;
  
  //background: green;
  
  display: flex;
  flex-direction: row;
  align-items: stretch;
  
  position: relative;
  left: 0;
  
  transition: left $trans-slow;
  
  &.transition-disabled {
    transition: none !important;
  }
  
  &[data-viewport="mainScreen"] {
    left: 0;
  }
  
  &[data-viewport="slideContent"] {
    left: -100vw;
  }
}

button {
  border: none;
  outline: none;
  cursor: pointer;
  transition: 200ms;
  
  &:hover {
    background: darken($color: white, $amount: 5);
  }
}


.sgs-blue-bg {
  background: $sgs-blue;
}

.custom-ul {
  margin: 0;
  padding: 0;
  
  display: flex;
  flex-direction: column;
  
  list-style-type: none;
  
  //background: rgba(crimson, .25);
  
  li {
    //background: rgba(cyan, .25);
    
    padding-left: 64px;
    margin-bottom: 20px;
  
    position: relative;
    
    &::before {
      position: absolute;
      content: '';
      left: 0;
      top: 2px;
      width: 20px;
      height: 22px;
      background: url("../../static/images/list-deco-blue.svg") no-repeat center center;
    }
  }
  
  &--skeleton {
    margin: 7px 0 0;
    padding: 0;
    
    
    li {
      //background: rgba(cyan, .25);
    
      padding-left: 30px;
      margin-bottom: 5px;
    
      position: relative;
    
      &::before {
        position: absolute;
        content: '';
        left: 0;
        top: 8px;
        width: 14px;
        height: 14px;
        background: url("../../static/images/list-deco-skeleton.svg") no-repeat center center;
      }
    }
  }
}

.overflow-x--hidden {
  overflow-x: hidden;
}






.marker {
  background-image: url('../../static/images/pin.svg');
  //background-image: url('../../static/images/mapbox-icon.png');
  background-size: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  width: 50px;
  height: 50px;
  //border-radius: 50%;
  cursor: pointer;
}

.mapboxgl-popup {
  max-width: 200px;
}

.mapboxgl-popup-content {
  text-align: center;
  font-family: $body-ff;
}






// SECTION 4 NAVIGATION CONTROLS
.slide-4__section-nav-controls {
  display: flex;
  position: fixed;
  //padding: 16px;
  
  //min-height: 80px;
  //background: palegoldenrod;
  
  right: 0;
  top: 0;
  
  z-index: 150;
  
  transition: 240ms;
  opacity: 0;
  visibility: hidden;
  transform: translateX(100%);
}

.slide-4__section-nav-controls--active {
  transition-delay: 560ms;
  opacity: 1;
  visibility: visible;
  transform: translateX(0%);
}


.s4-nav__button {
  width: 80px;
  height: 80px;
  position: relative;
  font-size: 0;
  color: transparent;
  
  transition: $trans-slow-half;
  
  &:first-of-type {
    //margin-right: 1px;
    border-right: 1px solid darken($sgs-blue, 10);
  }
  
  
  &.button--prev {
    background:
        url("../../static/images/slide4/nav-left-arrow--white.svg") no-repeat center center
        $sgs-blue;
    
    &:hover {
      background:
          url("../../static/images/slide4/nav-left-arrow--white.svg") no-repeat center center
          darken($sgs-blue, 5);
    }
  }
  
  &.button--next {
    background:
        url("../../static/images/slide4/nav-right-arrow--white.svg") no-repeat center center
        $sgs-blue;
    
    &:hover {
      background:
          url("../../static/images/slide4/nav-right-arrow--white.svg") no-repeat center center
          darken($sgs-blue, 5);
    }
  }
  
  
  &.button--disabled {
    pointer-events: none;
    opacity: .5;
    filter: grayscale(100%);
  }
}


.vector-map {
  position: relative;
  
  width: 100%;
  height: 100%;
  
  //background: palegoldenrod;
  
  svg {
    display: block;
    object-fit: contain;
    position: relative;
  
    //background: antiquewhite;
    background-image: url("../../static/images/map-bg.png");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center 50%;
    
    width: 100%;
    height: 100%;
  
  
    circle {
      transition: $trans-slow-half;
      fill: $sgs-blue;
    
      &:hover {
        fill: $red;
      }
    }
    path {
      transition: $trans-slow-half;
      fill: darken(#B1B1B1, 8);
    }
    rect {
      transition: $trans-slow-half;
      visibility: hidden;
      opacity: 0;
      fill: #021034;
    }
    .def-rect {
      opacity: 1;
      visibility: visible;
    }
  
    rect ~ path {
      visibility: hidden;
      opacity: 0;
      fill: white;
    }
  }
}


.vector-map--blue {
  svg {
    background-image: url("../../static/images/map-bg--white.png");
    
    path {
      fill: lighten(#B1B1B1, 20);
    }
    
    circle {
      fill: white;
    
      &:hover {
        fill: $red;
      }
    }
  }
}