.main-screen__contacts {
	height: 80px;
	box-sizing: border-box;
	flex-shrink: 0;
	
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	
	.contacts__phone-number {
		font-size: 18px;
		letter-spacing: .2px;
		
		position: relative;
		
		//background: palevioletred;
		
		width: 100%;
		height: 100%;
		
		display: flex;
		flex-direction: row;
		align-items: stretch;
		
		.number__city {
			opacity: 0.4;
			font-size: 14px;
		}
		
		a {
			color: white;
			text-decoration: none;
			
			//width: 50%;
			
			padding-left: 24px;
			padding-right: 24px;
			
			display: flex;
			flex-direction: column;
			//align-items: center;
			justify-content: center;
			
			transition: $trans-slow-half;
			
			&:nth-of-type(1) {
				//border-right: 1px solid rgba(white, 0.1);
				padding-left: 0;
				margin-right: 1px;
				
				position: relative;
				
				&::after {
					position: absolute;
					content: '';
					display: block;
					left: 100%;
					
					height: 52.5%;
					
					width: 1px;
					background: rgba(white, 0.1);
				}
			}
			
			&:hover {
				//background: rgba(white, 0.1);
			}
		}
	}
	
	.contacts__email {
		box-sizing: border-box;
		height: 100%;
		width: 80px;
		font-size: 0;
		
		padding-left: 28px;
		padding-right: 28px;
		
		display: flex;
		align-items: center;
		justify-content: center;
		
		border-radius: 0;
		border: 0;
		
		background: url('../../../static/images/email-icon.svg') no-repeat center center / 30%, transparent;
		border-left: 1px solid rgba($color: white, $alpha: .16);
	}
}