@media screen and (max-width: $mobile) {
	.content__slide-2 .slide-2--s1 {
		display: flex;
		flex-direction: column;
		padding-top: 80px;
	}
	
	.content__slide-2 .slide-2--s1 .grid-child--image-top,
	.content__slide-2 .slide-2--s1 .grid-child--image-bottom {
		display: none;
	}
	
	.content__slide-2 .slide-2--s1 .grid-child--list-item .list-item__number, .content__slide-2 .slide-2--s1 .grid-child--list-item .list-item__details {
		align-items: flex-start;
	}
	
	.content__slide-2 .slide-2--s1 .grid-child--list-item {
		margin-bottom: 48px;
		grid-template-columns: 30% 1fr;
	}
	
	.content__slide-2 .slide-2--s1 .grid-child--list-item .list-item__details {
		padding-right: 16px;
	}
	
	.content__slide-2 .slide-2--s2 .s2__title .title__image img {
		transform: translate3d(0,0,0) !important;
	}
	
	
	.content__slide-2 .slide-2--s2 {
		flex-direction: column;
		width: 100%;
	}
	
	.content__slide-2 .slide-2--s2 .s2__title {
		display: flex;
		flex-direction: column;
		margin: 0;
	}
	
	.content__slide-2 .slide-2--s2 .s2__title .title__image {
		margin-bottom: 48px;
	}
	
	.content__slide-2 .slide-2--s2 .s2__title .title__image::after {
		border-width: 48px;
		left: 50%;
	}
	
	.content__slide-2 .slide-2--s2 .s2__title .title__heading h2 {
		font-size: 36px;
		//margin-top: 32px;
		margin-bottom: 32px;
	}
	
	.content__slide-2 .slide-2--s2 .s2__custom-list {
		padding: 16px;
		display: flex;
		flex-direction: column;
	}
	
	.content__slide-2 .slide-2--s2 .s2__custom-list .custom-item__text {
		position: relative;
		margin-bottom: 32px;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		
		height: auto;
	}
	
	.content__slide-2 .slide-2--s2 .s2__custom-list .custom-item__text p {
		white-space: normal;
		padding-left: 48px;
		
		br {
			display: none;
		}
	}
	
	.content__slide-2 .slide-2--s2 .s2__custom-list .custom-item__text .custom-item__deco {
		top: 8px;
		left: -16px;
		
		transform: rotate(30deg);
	}
	
	.content__slide-2 .slide-2--s2 .s2__custom-list .custom-item__text .custom-item__line {
		display: none;
	}
}