@media screen and (max-width: $mobile) {
	.slide-4__item-description {
		display: block;
		
		margin: 0 16px 48px 16px;
		font-weight: bold;
		color: $dark-ocean;
		color: $dark-ocean;
		font-size: 16px;
	}
	
	.slide-4__competencies-title {
		display: block;
		
		background: #F3F5F8;
		text-transform: uppercase;
		font-weight: bold;
		color: #3D3D3D;
		padding: 24px;
		letter-spacing: 0.04em;
		line-height: 1;
	}
	
	.slide-4__section-nav-controls {
		display: none;
	}
	
	.s4-section {
		width: 100%;
		height: auto;
		padding: 0;
	}
	
	.s4-section h2 {
		margin: 16px 16px 16px 16px;
		padding-top: 80px;
		max-width: 100%;
		
		br {
			display: none;
		}
	}
	
	.s4-section p {
		margin: 16px;
		max-width: 100%;
		padding-left: 48px;
	}
	.s4-section p::before {
		width: 32px;
	}
	
	.content__slide-4 {
		transform: none !important;
		overflow: auto;
		position: relative;
	}
	
	.s4__section-navigation {
		display: none;
	}
	
	.s4-section .img__deco--small {
		display: none;
	}
	
	.s4-section .s4__img--secondary .img-deco-small {
		display: none;
	}
	
	.s4-section .img__deco--large {
		display: none;
	}
	
	.s4-section .s4__img--secondary {
		position: relative;
		visibility: visible;
		opacity: 1;
		width: 100%;
		right: 0;
		height: 70vw;
		margin-bottom: 12px;
	}
	
	.s4-section .s4__img--secondary img {
		height: 100%;
	}
	
	.s4-section .s4__img--secondary .img--secondary__wrapper {
		clip-path: none;
	}
	
	.s4-section--5 {
		flex-direction: column;
	}
	
	.s4-section--5 .map {
		width: 100%;
		height: auto;
	}
	
	.s4-section--5 .map .map-img {
		padding: 24px 16px;
	}
	
	.s4-section--5 .map p {
		margin: 0;
		padding: 24px 16px;
	}
	
	.s4-section--5 .article__wrapper {
		width: 100%;
		padding: 24px 16px;
		margin-bottom: 0;
	}
	
	.s4-section--5 .article__wrapper p {
		padding: 0;
		margin: 0;
	}
}