.s4-section--5 {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	//align-items: center;
	width: 100vw;
	padding: 40px 40px 40px 40px;
	
	p {
		padding: 0;
		max-width: 100%;
		
		
		&::before {
			display: none;
		}
	}
	
	& > div {
		//background: antiquewhite;
	}
	
	.map {
		flex-shrink: 0;
		height: 100%;
		width: 60%;
		
		display: flex;
		flex-direction: column;
		justify-content: space-evenly;
		//margin-right: 24px;
		
		.map-img {
			//background: lightcoral;
			
			//height: 75%;
			max-height: 85%;
			
			display: flex;
			justify-content: center;
			
			img {
				display: block;
				object-fit: contain;
				width: 100%;
				//height: calc((100vh - 80) * .75);
			}
		}
		
		p {
			display: block;
			//background: palegoldenrod;
			border-top: 1px solid rgba(black, .1);
			padding-top: 18px;
			font-style: italic;
			color: $content-light-subtle;
			
			margin-right: 6%;
		}
	}
	
	.article__wrapper {
		width: 35%;
		
		margin-top: 40px;
		margin-bottom: 40px;
		
		padding: 4vw;
		flex-shrink: 0;
		background: $sgs-blue;
		
		display: flex;
		align-items: center;
		
		overflow: hidden;
		
		p {
			color: white;
			//background: palevioletred;
			font-size: 22px;
			//font-size: 3vh;
			line-height: 1.5;
		}
	}
}