@media screen and (min-width: $fhd) {
	body {
		font-size: 24px;
	}
}

@media screen and (max-width: $mobile) {
	html,
	body {
		height: 100%;
		width: 100%;
		position: fixed;
		overflow: hidden;
		font-size: 15px;
	}
	
	
	body {
		-webkit-overflow-scrolling: touch;
	}
	
	.slide-content {
		overflow-y: auto;
		height: auto;
		//overflow-x: hidden;
		
		h2 {
			font-size: 24px;
			margin-bottom: 16px;
		}
	}
	
	.content__slide-2 h2 {
		//padding: 16px;
	}
	
	.slide-content .slide-content--wrapper {
		height: auto;
	}
	
	.slide-content .slide-content__slide {
		flex-direction: column;
		width: 100vw;
	}
}